body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f5f347;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 1500px;
}


@-webkit-keyframes move {
    100% {
        opacity: 1;
        -webkit-transform: translate(30%);
                transform: translate(30%);
    }
}

@keyframes move {
    100% {
        opacity: 1;
        -webkit-transform: translate(30%);
                transform: translate(30%);
    }
}

@-webkit-keyframes opacity1 {
    100% {
        opacity: 1;
    }
}

@keyframes opacity1 {
    100% {
        opacity: 1;
    }
}



.handycam-photo {
    opacity: .5;
}

@media only screen and (min-width: 960px) {
    .handycam-photo {
        position: absolute;
        right: 250px;
        width: 50vw; 
        top: 30px;
        -webkit-animation: move 1s forwards;
                animation: move 1s forwards;
    }
}

@media only screen and (max-width: 960px) {
    .handycam-photo {
        position: relative;
        left: 50%;
        margin-left: -40vw;
        width: 80vw; 
        top: -450px;
        -webkit-animation: opacity1 1s forwards;
                animation: opacity1 1s forwards;
    }
}


.Particles-Header-Background {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#45484d+0,000000+100;Black+3D+%231 */
    background: #45484d; /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #45484d 0%,#000000 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #45484d 0%,#000000 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#45484d', endColorstr='#000000',GradientType=0 ); /* IE6-9 */
}
.flex-box-container {
    position: relative;
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
    background-color: #fff;
    border-radius: 6px;
    box-shadow: 0 5px 5px 0 rgba(233,240,243,0.5), 0 0 0 1px #E6ECF8;
    color: #293347;
    display: block;
    padding: 1.25rem;
    display: flex;
}

.gamer-girl {
    width: 100%;

}

.p-e-e {
    width: 400px;
    margin-top: 100px;
    margin-bottom: 20px;
    margin-right: 0;
    font-size: 18px;
    line-height: 28px;
    font-weight: 400;
    color: #383838;
}

@media only screen and (min-width: 960px) {
    .flex-box-container {
        margin-top: -100px;

    }
}

@media only screen and (max-width: 960px) {
    .flex-box-container {
        margin-top: -400px;
    }
}

@media only screen and (min-width: 960px) {
    .lazy-center-box {
        border-left: 1px solid lightgray;
        border-right: 1px solid lightgray;
    }
}
/* body {
    font-family: 'Josefin Sans', sans-serif;
} */
.navbar {
    font-size: 18px;
    padding-bottom: 10px;
    -webkit-transition: .5s;
    transition: .5s;
    width: 100vw;
    z-index: 1;
}

.main-nav {
    list-style-type: none;
    padding-left: 0;
}

.nav-links {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.7);
    display: inline-block;
    font-size: 22px;
    margin-top: 10px;
    margin-left: 20px;
}

@media screen and (max-width: 768px) {
.navbar {
    z-index: 1;
    padding-bottom: 0;
    background-color: #05386B;
    position: absolute;
}

.navbar-toggle {
    position: absolute;
    top: 10px;
    right: 20px;
    cursor: pointer; 
    color: rgba(255,255,255,0.8);
    font-size: 24px;
}

.active-toggle {
    display: none;
}

.main-nav li {
    text-align: center;
    margin: 15px auto;
}

}
@media screen and (min-width: 768px) {
    .navbar {
        position: fixed;
        padding-bottom: 0;
        height: 70px;
        align-items: center;
    }

    .navbar-background {
        background: #05386B;
    }

    .main-nav {
        display: flex;
        line-height: 10px;
        margin-left: 30px;
        flex-direction: row;
        justify-content: flex-start;
    }

    .main-nav li {
        margin: 0;
    }

    .nav-links {
        margin-left: 40px;
    }

    .logo {
        margin-top: 0;
    }

    .navbar-toggle {
        display: none;
    }

    .logo:hover,
    .nav-links:hover {
        color: rgba(255, 255, 255, 1);
    }
}
.Footer {
    background-color: #05386B;
    height: 400px;
    padding-top: 50px;
    text-align: center;

}

.copyright {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #8F9AA7;
    margin-top: 80px;
}
.Video-Player {
    text-align: center;
    padding: 100px 0;
    background: #fff;
}

.video-image {
    -webkit-filter: brightness(70%);
            filter: brightness(70%);
    -webkit-transition: .25s;
    transition: .25s;
}

@media only screen and (min-width: 960px) {
    .video-image {
        height: 500px;
    }
}

@media only screen and (max-width: 960px) {
    .video-image {
        height: 250px;
    }
}


.video-image:hover {
    -webkit-filter: brightness(40%);
            filter: brightness(40%);
}
